import React from 'react'
import axios from 'axios'
import {makeStyles} from '@mui/styles';
import CardCategory from '../card/CardCategory';
import useWindowPosition from '../../hook/useWindowPosition'
import {useState, useEffect} from 'react'
import CardSkeleton from '../card/CitySkeleton';

const useStyles = makeStyles((theme) => ({
    root:{
        minHeight:'100vh',
        display: 'flex',
        flexWrap:'wrap',
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
/*         [theme.breakpoints.down('md')]: {
            flexDirection: 'column',
        }, */
    },

}));

export default function PlaceToVisit() {
    const classes = useStyles();
    const checked = useWindowPosition('header');
    const [city, setCity] = useState([])
    const [load, setLoad] = useState(false)

    useEffect(() => {
        let APIcategories = 'https://blog-react-demo.x10.mx/wp/wp-json/wp/v2/categories?parent=2'
        axios.get(APIcategories).then(response => {
            setCity(response.data)
            setLoad(true)
            })      
    },[])
    return (
        <div className={classes.root} id="place-to-visit">
            {load === false ? <CardSkeleton/> :
                city.map((item, index)=>(
                    <CardCategory key={index} index={index} place={item} checked={checked} />     
                ))
            }
  

      </div>
    )
}
